import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Router, ActivatedRoute } from "@angular/router";

import { Role, Relationship, AdminSideMenu, CommonExpands, Position } from "@enum/app_enum";
import { User } from "@model/user";
import { UserService } from "@core/services/serviceIndex";
import { AuthenticationService } from "@core/authentication/authentication.service";
import Croppie from "croppie";
import { Subscription } from 'rxjs';

declare var $: any;
@Component({
  selector: 'app-user-create',
  templateUrl: './user-create.component.html',
  styleUrls: ['./user-create.component.css']
})
export class UserCreateComponent implements OnInit, OnDestroy {
  public isCrop: boolean = false;
  blankCrop: boolean = false;
  imgDemoSlider: boolean = false;
  validProfile: boolean = false;
  public input: any;
  public uploadCrop: any;
  public uploadImg: any;
  public userForm: FormGroup
  public submitted = false;
  public userRole = [Role.Admin, Role.Trainer]
  public position = [Position.Office_Staff, Position.Seniorteacher];
  public relationship = [Relationship.Mother, Relationship.Father]
  public routeDirLists: any = {
    routeName: [AdminSideMenu.User, CommonExpands.Create],
    routeLink: ["kmax/users/lists/admin"]
  }
  public userId: any;
  public isChangePassword: boolean;
  public routeRole: any;
  public subscription: Subscription;
  public techanics: any = ['Cardio', 'Boxing', 'Cirauit', 'Tabata', 'Step brand', 'Funtional', 'T R H swapersion traning', 'Weight gift day'];
  public selectedRole: any;

  constructor(private formBuilder: FormBuilder, private _userService: UserService,
    private _router: Router, private _activeRoute: ActivatedRoute, private _auth: AuthenticationService) { }

  ngOnInit(): void {
    this.userForm = this.formBuilder.group({
      username: ["", [Validators.required]],
      email: ["", [Validators.required, Validators.email]],
      role: ["", [Validators.required]],
      gender: ["male", [Validators.required]],
      password: ["", [Validators.required, Validators.minLength(5)]],
      confirmPassword: [""],
      dateOfBirth: ["", [Validators.required]],
      description: ["", [Validators.required]],
      height: ["", [Validators.required]],
      hmeasurement: ["", [Validators.required]],
      wmeasurement: ["", [Validators.required]],
      weight: ["", [Validators.required]],
      techanics: ["", [Validators.required]],
      address: ["", [Validators.required]],

    },
      {
        validator: MustMatch("password", "confirmPassword"),
      }
    );
    this.checkAction();
  }

  checkAction() {
    this._activeRoute.params.subscribe(params => {
      this.userId = params.userId;
      this.routeRole = params.role;
      if (this.userId) {
        this.getUserDetail();
      }
    });
  }

  getUserDetail() {
    this.subscription = this._userService.getUserDetail(this.userId, this.routeRole)
      .subscribe((res) => {
        let user = res.user;
        this.uploadImg = user.image;
        this.selectedRole=user.userType;
        this.userForm = this.formBuilder.group({
          username: [user.username, [Validators.required]],
          email: [user.email, [Validators.required, Validators.email]],
          role: [user.userType, [Validators.required]],
          gender: [user.gender, [Validators.required]],
          password: ["", [Validators.required, Validators.minLength(5)]],
          confirmPassword: [""],
          dateOfBirth: [user.dateOfBirth, [Validators.required]],
          description: [user.description ? user.description : "", [Validators.required]],
          address: [user.address ? user.address : "", [Validators.required]],
          height: ["", [Validators.required]],
          hmeasurement: ["", [Validators.required]],
          wmeasurement: ["", [Validators.required]],
          weight: ["", [Validators.required]],
          techanics: ["", [Validators.required]],
        },
          {
            validator: MustMatch("password", "confirmPassword"),
          }
        );
      })
  }

  onSubmit() {
    if (this.userForm.value.role == Role.Admin) {
      ['description', 'height', 'hmeasurement', 'weight', 'wmeasurement', 'techanics'].map(d => this.userForm.controls[d].setErrors(null))
    } else
      ['address'].map(d => this.userForm.controls[d].setErrors(null));

    if (this.userId && !this.isChangePassword)
      this.userForm.controls['password'].setErrors(null);

    this.submitted = true
    if (this.userForm.invalid) return;
    else {
      if (this.userId) {
        this.onUpdate();
      } else {
        this.onCreate();
      }
    }
  }

  getFinalData() {
    let user = new User();
    user.username = this.userForm.value.username;
    user.email = this.userForm.value.email;
    user.gender = this.userForm.value.gender;
    user.dateOfBirth = this.userForm.value.dateOfBirth;
    if (this.userForm.value.password)
      user.password = btoa(this.userForm.value.password);

    user.userType = this.userForm.value.role;
    this.selectedRole = user.userType;
    if (this.userForm.value.role == Role.Trainer) {
      user.description = this.userForm.value.description;
      user.techanics = this.userForm.value.techanics;
      user.height = this.userForm.value.height + " " + this.userForm.value.hmeasurement;
      user.weight = this.userForm.value.height + " " + this.userForm.value.wmeasurement;
    } else {
      user.address = this.userForm.value.address;
    }
    let getImg = document.getElementById("blobUrl");
    let img;
    img =
      getImg != undefined
        ? document.getElementById("blobUrl").getAttribute("src")
        : (img = this.uploadCrop);
    if (img != undefined) {
      let ulFile = this.dataURItoBlob(img);
      user.file = ulFile;
    }
    return user;
  }

  onCreate() {
    let finalData = this.getFinalData();
    this.subscription = this._userService.createUser(finalData)
      .subscribe(
        () => {
          this.goBack();
        });
  }

  onUpdate() {
    let finalData = this.getFinalData();
    finalData._id = this.userId;
    this.subscription = this._userService.updateUser(finalData)
      .subscribe(
        () => {
          this.goBack();
          //this.userForm.reset()
        });
  }



  get f() { return this.userForm.controls }


  uploadCropImg($event: any) {
    var image: any = new Image();
    this.blankCrop = true;
    $(".frame-upload").css("display", "block");
    this.imgDemoSlider = true;
    $("#upload-demo img:first").remove();
    this.input = $event.target.files[0];
    // if (this.input.size <= 477732 && this.input) {
    if (this.input && this.uploadCrop) {
      this.uploadCrop.destroy();
    }
    var reader: FileReader = new FileReader();
    this.uploadCrop = new Croppie(document.getElementById("upload-demo"), {
      viewport: {
        width: 120,
        height: 120,
        type: "circle",
      },
      boundary: {
        width: 120,
        height: 120,
      },
      enableExif: true,
    });

    var $uploadCrop = this.uploadCrop;
    reader.onload = function (e: any) {
      $(".upload-demo").addClass("ready");
      $uploadCrop
        .bind({
          url: e.target.result,
        })
        .then(function (e: any) { });
    };
    reader.readAsDataURL($event.target.files[0]);
    // } else {
    //   console.log('file size is too large');
    //   // this.toastr.error('file size is too large');
    //   this.validProfile = false;
    //   this.imgDemoSlider = false;
    //   $('.frame-upload').css('display', 'none');
    // }
  }

  cropResult() {
    this.validProfile = true;
    this.isCrop = true;
    let self = this;


    this.imgDemoSlider = false;
    setTimeout(function () {
      $(".circular-profile img:last-child").attr("id", "blobUrl");
      $(".frame-upload").css("display", "none");
      this.blankCrop = false;
    }, 200);

    var cropper = this.uploadCrop;
    var BlobUrl = this.dataURItoBlob;
    this.uploadCrop
      .result({
        circle: false,
        type: "canvas",
        size: {
          width: 120,
          height: 120,
        },
        quality: 1,
      })
      .then(function (resp: any) {
        if (resp) {
          setTimeout(function () {
            $(".circular-profile i").remove();
            $(".circular-profile img").remove();

            $(".circular-profile").append(
              '<img src="' + resp + '" width="100%" />'
            );
          }, 100);
        }
      });
  }

  dataURItoBlob(dataURI: any) {
    var byteString = atob(dataURI.split(',')[1]);
    var mimeString = dataURI
      .split(',')[0]
      .split(':')[1]
      .split(';')[0];
    var ab = new ArrayBuffer(byteString.length);
    var ia = new Uint8Array(ab);
    for (var i = 0; i < byteString.length; i++) {
      ia[i] = byteString.charCodeAt(i);
    }
    return new Blob([ab], { type: mimeString });
  }

  backToUpload() {
    // this.hideMenu = true;
    this.validProfile = false;
    this.imgDemoSlider = false;
    $(".frame-upload").css("display", "none");
  }



  goBack() {
    if(this.selectedRole)
    this._router.navigateByUrl('kmax/users/lists/' + this.selectedRole);
    else 
    this._router.navigateByUrl('kmax/users/lists/admin');

  }

  showForget() {
    this.isChangePassword = true;
  }

  ngOnDestroy() {
    if (this.subscription)
      this.subscription.unsubscribe();
  }
}

function MustMatch(controlName: string, matchingControlName: string) {
  return (formGroup: FormGroup) => {
    const control = formGroup.controls[controlName];
    const matchingControl = formGroup.controls[matchingControlName];

    if (matchingControl.errors && !matchingControl.errors.mustMatch) {
      // return if another validator has already found an error on the matchingControl
      return;
    }

    // set error on matchingControl if validation fails
    if (control.value !== matchingControl.value) {
      matchingControl.setErrors({ mustMatch: true });
    } else {
      matchingControl.setErrors(null);
    }
  };

}
